*{
margin: 0px;
padding: 0px;
box-sizing: border-box;
text-decoration: none;
}
.header {
    background-color: rgb(255, 146, 146);
    display: flex;
    align-items: center;
    font-size: 25px;
    height: 60px;
    width: 100%;
    list-style-type: none;
    text-decoration: none;

}
.list{
    margin: 20px;
    color: white;
    position: relative;
}
.Main{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
.main{
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.main img{
    width: 200px;
    height: 200px;
}
.iconLength{
    font-size: 18px;
    padding: 2px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    right: -20px;
    top: 0px;
    width: 20px;
    height: 20px;
    background-color: rgb(89, 33, 255);
}
.quantity button{
    width: 20px;
    margin: 10px;

}